.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(15, 15, 35, 0.9);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  z-index: 1000;
  padding: 1.2rem 0;
  box-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Fallback for browsers that don't support backdrop-filter */
@supports not (backdrop-filter: blur(20px)) {
  .header {
    background: rgba(15, 15, 35, 0.95);
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  }
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.auth-section {
  display: flex;
  align-items: center;
}

.user-section {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-primary);
  font-weight: 500;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.user-info-button {
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: inherit;
  font-size: inherit;
}

.user-info-button:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.2);
}

.user-info-button:active {
  transform: translateY(0);
}

.user-icon {
  font-size: 1.2rem;
  line-height: 1;
}

.user-name {
  font-size: 0.95rem;
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.premium-badge {
  background: linear-gradient(135deg, #00d9ff, #0099cc);
  color: white;
  font-size: 0.65rem;
  font-weight: 700;
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 8px rgba(0, 217, 255, 0.4);
  animation: premiumGlow 2s ease-in-out infinite;
}

@keyframes premiumGlow {
  0%, 100% {
    box-shadow: 0 2px 8px rgba(0, 217, 255, 0.4);
  }
  50% {
    box-shadow: 0 2px 15px rgba(0, 217, 255, 0.6);
  }
}

.login-link {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 600;
  padding: 0.6rem 1.5rem;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(233, 69, 96, 0.3);
}

.login-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(233, 69, 96, 0.4);
}

.logout-button {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 0.6rem 1.2rem;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: inherit;
}

.logout-button:hover:not(:disabled) {
  background: rgba(233, 69, 96, 0.2);
  border-color: var(--primary-color);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(233, 69, 96, 0.3);
}

.logout-button:active:not(:disabled) {
  transform: translateY(0);
}

.logout-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.logo {
  text-decoration: none;
  font-family: 'Orbitron', sans-serif;
  font-size: 2rem;
  font-weight: 900;
  display: flex;
  align-items: baseline;
  transition: all 0.3s ease;
  position: relative;
}

.logo-text {
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.logo-text.accent {
  color: var(--primary-color);
  margin-left: 2px;
}

.logo-dot {
  color: var(--secondary-color);
  font-size: 1.5rem;
  margin-left: 2px;
}

.logo:hover .logo-text {
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

.logo:hover .logo-text.accent {
  text-shadow: 0 0 20px rgba(233, 69, 96, 0.8);
}

.nav {
  display: flex;
  gap: 2rem;
}

.nav-link,
.nav-button {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
  padding: 0.5rem 0;
  background: none;
  border: none;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
}

.nav-link::after,
.nav-button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  transition: width 0.3s ease;
}

.nav-link:hover,
.nav-button:hover {
  color: var(--text-primary);
}

.nav-link:hover::after,
.nav-button:hover::after {
  width: 100%;
}

.settings-button {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--text-secondary);
}

.settings-button:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: var(--secondary-color);
  color: var(--secondary-color);
  transform: scale(1.1) rotate(45deg);
  box-shadow: 0 5px 15px rgba(0, 217, 255, 0.3);
}

.settings-button:active {
  transform: scale(0.95) rotate(45deg);
}

/* Responsive */
@media (max-width: 768px) {
  .logo {
    font-size: 1.5rem;
  }

  .header-right {
    gap: 1rem;
  }

  .nav {
    gap: 1rem;
  }

  .nav-link {
    font-size: 0.9rem;
  }

  .user-name {
    max-width: 100px;
  }

  .logout-button {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
  }

  .settings-button {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 600px) {
  .nav {
    display: none;
  }

  .header-right {
    gap: 0.5rem;
  }

  .user-info {
    padding: 0.4rem 0.8rem;
  }

  .user-name {
    display: none; /* Hide username on very small screens, keep icon */
  }

  .logout-button {
    padding: 0.5rem 0.8rem;
    font-size: 0.8rem;
  }

  .login-link {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }
} 